body {
  font-family: "Open Sans", "Helvetica", "sans-serif";
  padding-bottom: 200px;
  background: -webkit-gradient(linear, left top, left bottom, from(#ffffff),to(#a6b5bc));
  background: -webkit-linear-gradient(top, #ffffff 0%,#a6b5bc 100%);
  background: linear-gradient(to bottom, #ffffff 0%,#a6b5bc 100%);
}

h1, h2, h3, p {
	font-family: "Montserrat", "sans-serif";
}
h1 {
	padding: 30px;
}
h3, button {
	margin: 10px;
}
p {
  font-size: 1.2em;
  line-height: 1.6em;
}
p strong {
  font-size: 1.25em;
}
header {
	background: #ffffff url(/poem/static/media/background.a0f45977.jpg);
	background-size: cover;
  background-position: center;
	display: flex;
	width: 100%;
	height: 90vh;
	flex-direction: column;
	align-items: center;
	justify-content: center;
  box-shadow: 1px 1px 3px black;
  opacity: 0.95;
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  resize: both;
}
header h1 {
	text-align: center;
	align-self: center;
	font-size: 70px;
	font-weight: bold;
}
.container {
	width: auto;
	margin: 0px;
	display: flex;
	flex-direction: column;
	align-items: center;
}
#main-title {
	margin-top: 60px;
	margin-bottom: 10px;
  color: white;
}
#languages {
	float: right;
	/*position: absolute;
	right: 10px;
	top: 15px;
	margin: 10px 0px;*/
}
#languages img {
  height: 4em;
  width: 4em;
}
#h-btn-container {
	margin: 20px 20px 50px 20px;
	padding: 20px;
}
#poem-container {
	margin: 50px;
}
#poem-container img {
  border-radius: 10px;
  border: gray solid 1px;
}
#instruction {
	padding: 40px;
}
#img-container {
  margin: 1.5em;
}

